.ListaSuspensa label{
     display: block;
    margin-bottom: 8px;
    font-size: 16pt;
    font-family: monospace;
    letter-spacing: 1px;
    text-transform: uppercase;
}
.ListaSuspensa select{
    background-color: #FFF;
    box-shadow: 10px 10px 30px rgba(0, 0, 0, 0.06);
    width: 100%;
    border: none;
    font-size: 24px;
    padding: 24px;
    box-sizing: border-box;
    border-radius: 10px ;

}